.sidebar {
  flex: 3;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fdfbfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .sidebarItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    .sidebarTitle {
      margin: 10px;
      padding: 5px;
      width: 80%;
      border-top: 1px solid #a7a4a4;
      border-bottom: 1px solid #a7a4a4;
      text-align: center;
    }

    .sidebarImg {
      margin-top: 15px;
      width: 200px;
      height: 200px;
      object-fit: cover;
    }
    p {
      padding: 30px;
    }
    .sidebarList {
      list-style: none;
      margin-bottom: 30px;

      .sidebarListItem {
        display: inline-block;
        width: 50%;
        margin-top: 15px;
        cursor: pointer;
        text-align: center;
      }
    }
    .sidebarSocial {
      margin-top: 15px;
      width: 250px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 30px;

      .sidebarItem {
        margin-right: 10px;
        margin-left: 10px;
        display: flex;
        flex-wrap: wrap;
      }
    }
  }
}
